body > svg {
  display: none;
}

.svg-basic {
  max-width: 26px;
  max-height: 26px;
  fill: $gray-light;
  @extend .transition;
}

.svg-nav {
  fill: $blue-light;
}

.media__img--svg {
  margin-right: $inuit-base-spacing-unit--small;
}

.svg-main-logo {
  fill: white;
  position: relative;
  top: 8px;
}

.svg--alert {
  fill: $alert-danger-bg;
}

.svg--success {
  fill: $alert-success-bg;
}

.svg--blue {
  fill: $blue-dark;
}

.svg--white {
  fill: $white;
}
.btn{
  .svg-basic{
    position: relative;
    bottom: -$inuit-base-font-size/3;
    margin-right: $inuit-base-spacing-unit--tiny;
  }
}
.svg--avatar--callout{
    width: 100%;
    height: 100%;
}

.svg--avatar{
    width: 100%;
    height: 100%;
}
.svg--avatar--user-register{
    min-height: 200px;
}
.hide-modal--top__svg{
  max-width: 40px;
  max-height: 40px;
}